.title {
  color: white;
  font-size: medium;
  font-weight: 500;
  text-align: left;
  padding-left: 20px;
  padding-bottom: 20px;

  justify-content: center;
}
.header {
  display: flex;
  justify-content: space-between;
  border-radius: 10px;
  border: 0.5px solid rgb(116, 112, 112);
  width: 500px;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
}
.scorecard {
  width: 120px;
  height: 80px;
  border-radius: 5px;
  background-color: aliceblue;

  margin-right: 20px;
  margin-top: 10px;
  display: block;
}
.score {
  margin-bottom: 10px;
  font-size: 40px;
  font-weight: 700;
  margin-top: 0px;
}
.scoretitle {
  font-size: 12px;
  color: rgb(72, 72, 162);
  font-weight: 600;
  margin-bottom: 0px;
}
.paper {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 10px solid green;
  background-image: url("../images/paper.png");
  background-size: 60%;
  background-repeat: no-repeat;
  background-position: center;
  background-color: white;
  cursor: pointer;
}

.scissors {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 10px solid orange;
  background-image: url("../images/scissors.png");
  background-size: 60%;
  background-repeat: no-repeat;
  background-position: center;
  background-color: white;
  cursor: pointer;
}
.rock {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 10px solid yellow;
  margin-left: 80%;
  background-image: url("../images/rock.png");
  background-size: 60%;
  background-color: white;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}
.rock:hover {
  box-shadow: rgba(230, 216, 216, 0.35) 0px 5px 15px;
  border: 10px solid green;
}
.paper:hover {
  box-shadow: rgba(230, 216, 216, 0.35) 0px 5px 15px;
  border: 10px solid orange;
}
.scissors:hover {
  box-shadow: rgba(230, 216, 216, 0.35) 0px 5px 15px;
  border: 10px solid yellow;
}
.choose {
  height: 350px;
  width: 400px;
  display: grid;
  grid-template-columns: auto auto;
  column-gap: 80px;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  position: relative;
}
.connector1 {
  width: 80px;
  height: 10px;
  background-color: rgb(16, 16, 41);
  position: absolute;
  left: 40%;
  top: 10%;
}
.connector2 {
  width: 80px;
  height: 10px;
  background-color: rgb(16, 16, 41);
  position: absolute;
  left: 35%;
  top: 39%;
  transform: translate(-50%, -50%) rotate(60deg);
}
.connector3 {
  width: 80px;
  height: 10px;
  background-color: rgb(16, 16, 41);
  position: absolute;
  left: 65%;
  top: 40%;
  transform: translate(-50%, -50%) rotate(120deg);
}
@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }

  100% {
    opacity: 1;
    transform: translateY(0px);
    width: 120px;
    height: 120px;
  }
}

.user {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 10px solid green;
  background-color: white;
  background-size: 60%;
  background-position: center;
  background-repeat: no-repeat;
}
.computer {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 10px solid orange;
  background-color: white;
  background-size: 60%;
  background-position: center;
  background-repeat: no-repeat;
}
.computer:hover {
  box-shadow: 5px 5px 5px rgb(83, 73, 55);
  border: 11px solid green;
}
.user:hover {
  box-shadow: 5px 5px 5px rgb(83, 73, 55);
  border: 11px solid orange;
}
.check {
  display: flex;
  margin-top: 100px;

  justify-content: center;
  column-gap: 200px;
}
.result {
  margin-top: 50px;
  width: 200px;
  height: 50px;

  animation: countdown 1s;
  animation-iteration-count: 4;
  position: absolute;
  font-weight: 800;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  color: gold;
}
@keyframes countdown {
  0% {
    font-size: 20px;
  }
  25% {
    font-size: 25px;
  }
  50% {
    font-size: 30px;
  }

  100% {
    font-size: 35px;
  }
}

.try {
  margin-top: 100px;
  margin-left: 20px;
  margin-right: auto;
  border-radius: 5px;
  width: 100px;
  height: 50px;
  font-size: 15px;
  font-weight: 400;
  background-color: transparent;
  color: white;
  border: 1px solid white;
}
.try:hover {
  background-color: white;
  color: rgb(31, 31, 57);
}
.rules {
  margin-top: 20px;
  border-radius: 5px;
  width: 100px;
  height: 50px;
  font-size: 15px;
  font-weight: 400;
  background-color: transparent;
  color: white;
  border: 1px solid white;
  left: 90%;
  top: 77%;
  align-self: baseline;
  position: absolute;
}
.modal {
  position: absolute;
  left: 35%;
  top: 30%;
  background-color: white;
  padding: 50px;
}

.close {
  margin-left: 400px;
}
